'use strict';
/**
 * @api TOAST(message,type) TOAST
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiParam {string} message 文本
 * @apiParam {string} type=3 提示类型 0:对勾 1:错误 2:感叹号 3:无图标
 * @apiDescription app toast 事件
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * TOAST('手机号格式错误')
 * @apiParamExample {string} 返回值
 * 无
 **/

/**
 * todo TOOLS
 */
import {WinJSDec} from './APP/config';

var name = 'TOAST';
var TOAST = (message,offSet = 100) => {
  WinJSDec(_ => {
    window.WinJSBridge.call('tools', 'toast', {message, offSet});
  });
};
export default TOAST;
